import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
// eslint-disable-next-line no-unused-vars
import swal from 'sweetalert'
import Button from '../components/Form/Button'
import Label from '../components/Form/Label'
import TextField from '../components/Form/TextField'


const EditSpotForm = () => {
    const [location, setLocation] = useState({});
    const { id } = useParams();
    const history = useHistory();

    useEffect(() => {
        fetch(`http://localhost:5000/location/${id}`)
            .then(res => res.json())
            .then(data => setLocation(data));
    }, [id])

    //track the update 
    const handleUpdate = (e) => {
        const { value, name } = e.target

        setLocation((prev) => {
            return {
                ...prev,
                [name]: value
            }
        })
    }

    //handleUpdate 
    const handleSubmit = e => {
        e.preventDefault();
        const name = location.name;
        const star = location.star;
        const cost = location.cost;
        const img = location.img;
        const country = location.country;

        const updateSpot = { name, star, cost, img, country }

        //update 
        fetch(`http://localhost:5000/location/${id}`, {
            method: 'PUT',
            headers: {
                'content-type': 'application/json',
            },
            body: JSON.stringify(updateSpot)
        }).then(res => res.json())
            .then(data => {
                if (data.modifiedCount > 0) {
                    swal("Updated", "Update Successfully!", "success");
                }
                history.push('/admin/manage-products');
            })
    }



    return (
        <>
            <form className="grid grid-cols-1 md:grid-cols-1 lg:grid-cols-2 gap-10 mt-6" onSubmit={handleSubmit}>
                {/* title and description  */}
                <div className="flex flex-col space-y-4">
                    <Label htmlFor="title" text="Spot Title" />
                    <TextField
                        id="title"
                        type="text"
                        name="title"
                        value={location.name || " "}
                        onChange={handleUpdate}
                        required />

                    {/* description  */}
                    <Label htmlFor="description" text="Spot Description" />
                    <textarea
                        id="description"
                        cols="30" rows="9"
                        className="border border-gray-200 rounded-lg py-3 px-4 w-full focus:outline-none ring-red-200 transition duration-500 focus:ring-4 resize-none"
                        name="description"
                        value={location.country || " "}
                        onChange={handleUpdate}
                    >

                    </textarea>
                </div>

                {/* price , image , type  */}
                <div className="flex flex-col space-y-4">
                    {/* price  */}
                    <Label htmlFor="price" text="Spot cost" />
                    <TextField
                        id="price"
                        type="number"
                        name="price"
                        value={location.cost || " "}
                        onChange={handleUpdate}
                        required />
                    {/* image url  */}
                    <Label htmlFor="image" text="Spot Image URL" />
                    <TextField
                        id="image"
                        type="text"
                        name="image"
                        value={location.img || " "}
                        onChange={handleUpdate} required />
                    {/* type  */}


                    <Label htmlFor="type" text="Spot" />
                    <TextField
                        id="type"
                        className="border border-gray-200 rounded-lg py-3 px-4 w-full focus:outline-none ring-red-200 transition duration-500 focus:ring-4"
                        value={location.star || " "}
                        name="spotType"
                        onChange={handleUpdate}

                    />

                    {/* button  */}
                    <div className="mt-8">
                        <Button text="Update" type="submit" />
                    </div>
                </div>
            </form>
        </>
    )
}

export default EditSpotForm
